@import url(https://fonts.googleapis.com/css?family=DM+Sans:400,500,700&display=swap);
:root{--clr-primary: #4ade80;--clr-primary-light1: #f0fdfa;--clr-primary-light2: #ccfbf1;--clr-neutral-white: hsl(0, 0%, 100%);--clr-neutral-text: #4b5563;--clr-neutral-title: #111827;--fs-main-desktop: 15px;--ff-main: "DM Sans", sans-serif}*,*::before,*::after{padding:0;margin:0;box-sizing:border-box}html{font-size:15px;font-size:var(--fs-main-desktop)}body{font-family:"DM Sans", sans-serif;font-family:var(--ff-main);min-height:100vh;background-color:#0ea5e9;background:linear-gradient(90deg, #5eead4, #14b8a6)}.container{margin:auto auto;width:100%;max-width:600px;min-height:95vh;display:flex;justify-content:center;align-items:center;flex-direction:column}.card{background-color:hsl(0, 0%, 100%);background-color:var(--clr-neutral-white);box-shadow:0px 10px 15px rgba(0,0,0,.1),0px 4px 6px rgba(0,0,0,.05);border-radius:1rem;display:flex;flex-direction:column;padding:1.2rem;width:70%}.header{display:flex;flex-direction:row;justify-content:space-between;margin:1rem 0}.header .title{font-size:1.3rem;font-weight:500;color:#111827;color:var(--clr-neutral-title)}.header .text-gray{color:#4b5563;color:var(--clr-neutral-text);font-size:1rem}.tool-bar{display:flex;flex-direction:row;justify-content:space-between;color:#4b5563;color:var(--clr-neutral-text);margin:.5rem 0;font-size:1rem}.tool-bar .filters{display:flex;flex-direction:row}.tool-bar .filter{border:0;padding:3px 8px;background:0;line-height:1;cursor:pointer;font-family:var(--font);color:#8a9ca5;border-radius:20px}.tool-bar .filter[aria-pressed=true]{border:0;padding:3px 8px;line-height:1;cursor:pointer;font-family:var(--font);border-radius:20px;background-color:#4ade80;background-color:var(--clr-primary);color:#fff}.add-task{padding:.5rem;border:0;box-shadow:0 -1px 0 #e2e4ea inset;outline:none !important;margin-top:.5rem;font-size:1rem;width:100%}.add-task ::-webkit-input-placeholder{color:#8a9ca5}.add-task :-ms-input-placeholder{color:#8a9ca5}.add-task ::placeholder{color:#8a9ca5}.add-task:focus{box-shadow:0 -1px 0 